@font-face {
  font-family: 'Cinnamon Coffe Coke';
  src: url("assets/CinnamonCoffeCake.ttf");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: flex;
  width: 100%;
  height: auto;
  min-height: 100vh;
}

#root > nav {
  /* block */
  width: 42px;
  height: inherit;
  padding: 7px;
  overflow: hidden;
  white-space: nowrap;

  /* appearance */
  background: rgb(2,0,36);
  background: linear-gradient(65deg, rgba(196,113,113,1) 0, rgba(2,0,36,1) 95%);
  font-size: 22px;

  /* animation */
  transition: 300ms;
}

#root > nav:hover, #root > nav:focus {
  outline: none;    /* don't show when focused */
  width: 250px;
}

#root > nav > ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

#root > nav > ul > li {
    margin-bottom: 10px;
}

#root > nav > ul > li > button {
  background-color: transparent;
  border: none;
  display: flex;
  align-items: center;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 0;
  width: 100%;
  font-family: "Cinnamon Coffe Coke", serif;
}

#root > nav > ul > li > button > img {
  max-width: 40px;
  max-height: 40px;
}

#root > nav > ul > li > button > div {
  flex-grow: 1;
  margin: 0 8px;
  padding: 5px;
  transition: 100ms;
}

#root > nav:hover > ul > li > button > div:hover {
    background-color: rgba(106, 90, 205, 0.30);
    background-image: url("assets/nav-bar/hover.gif");
}

#root > main {
  flex-grow: 1;
  padding: 4px 8px;
}
